import React from 'react'
import {Link} from 'react-router-dom'


export default function Services1() {
  return (
    <div className='container d-flex' style={{alignItems:"center",justifyContent:"center",height:"600px",width:"400px",border:"5px"}}>
        <form method="post" action="/first">
            <label htmlFor="username">Please enter your username</label>
                <input type="text" id='username' name='Username'/>
            <label htmlFor="bb">Please enter the amount of your first deposit</label>
            <input type="text" id='bb' name='bankBalance'/>
            <button className='btn btn-dark'><Link to="/services2">Go to Services</Link></button>
        </form>
      
    </div>
  )
}


